.App {
  text-align: left;
}

.logo {
  animation: logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.header {
  background-color: #282c34;
  min-height: 5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.homelink {
  text-decoration: none;
  color: #3fa0b3;
  text-align: left;
  margin-top: -3px;
  cursor: pointer;
  font-size: 2.0rem;
}
.homelink:hover {
  color: #3fa0b3;
  text-decoration: none;
  font-weight: bold;
}
.menulink {
  text-decoration: none;
  color: #fff;
  text-align: left;
  font-size: 1.0rem;
  cursor: pointer;
}
.menulink:hover {
  color: #3fa0b3;
  text-decoration: none;
  font-weight: bold;
}
.active {
  color: #3fa0b3;
  text-decoration: none;
  font-weight: bold;
}
.inactive {
  color: #fff; 
  text-decoration: none;
}


@keyframes logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
